<template>
  <section
    class="who-are-we"
    :class="{'video-mode' : playStatus}"
  >
    <div class="container">
      <div class="row align-items-center">
        <div
          class="col-sm-6 col-12 text-content"
          :class="{'col-xl-12' : clicked}"
        >
          <h2 class="text-blue">Who are we?</h2>
          <p><strong>We offer extensive medical procedures to outbound and inbound patients.</strong></p>
          <p>The passage experienced a surge in popularity during the 1960s when Letraset used it on their dry-transfer sheets, and again during the with their software.</p>
          <a
            href="javascript:;"
            class="btn btn-blue"
          >View Details</a>
        </div>

        <div
          @click="playVideo"
          class="tr-3s col-xl-5 col-sm-6 col-12"
          :class="{'col-xl-12' : clicked , 'offset-xl-1' : !clicked}"
        >
          <div class="video-cover text-white text-center">

            <img
              v-if="!clicked"
              src="https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQNGkTg_OQZYMAdflVFpAaqsJZ40Zy65wMalahILAO0oWMlUrUk"
              alt=""
              srcset=""
            >
            <div
              v-if="!clicked"
              class="image-desc"
            >
              <h2>Who are we?</h2>
              <p><strong class="d-none d-md-block">We offer extensive medical procedures to outbound and inbound patients.</strong></p>
            </div>
            <transition
              name="slideIn"
              appear
            >
              <div
                v-show="clicked"
                class="video-wrapper"
              >
                <video
                  ref="video"
                  lazy-src="https://drsacdn.mncdn.com/videos/DokuMainPage.mp4"
                ></video>
              </div>
            </transition>
          </div>
        </div>

      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      clicked: false,
      playStatus: false
    };
  },
  methods: {
    playVideo() {
      this.clicked = true;
      let v = this.$refs.video;
      v.setAttribute("src", v.getAttribute("lazy-src"));
      if (this.playStatus == false) {
        this.playVid();
        this.playStatus = true;
        setTimeout(() => {
          window.screen.width > 768
            ? v.scrollIntoView({ behavior: "smooth" })
            : v.scrollIntoView({ behavior: "smooth", block: "center" });
        }, 50);
      } else {
        v.pause();
        this.playStatus = false;
      }
    },
    playVid: async function() {
      let v = this.$refs.video;
      try {
        await v.play().then();
        // Get a loader here maybe
      } catch (err) {}
    }
  }
};
</script>

