<template>
  <section
    v-if="lightboxVisibility"
    class="lightBox"
    ref="lightBox"
    @scroll="instagramScroll"
  >
    <div
      @keyup.esc="closeLightbox"
      class="light-box"
    >
      <div
        v-if="lightboxData"
        class="container"
      >
        <div
          class="inner "
          v-for="p in lightboxData"
          :key="p.id"
          :id="p.id"
        >
          <swiper
            v-if="p['media_type'] == 8"
            ref="insSwiper"
            :options="swiperOption"
          >
            <swiper-slide
              v-for="m in p['carousel_media']"
              :key="m.id"
            >
              <video
                @mouseover="strictPlay"
                v-if="m['media_type'] == 2"
                @click="playVid"
                :src="m['video_versions'][0].url"
                :poster="p['image_versions']['815']"
              ></video>
              <img
                v-else
                :src="m['image_versions2']['candidates'][1].url"
                alt=""
                srcset=""
              >
            </swiper-slide>
            <div
              class="swiper-pagination"
              slot="pagination"
            ></div>
          </swiper>
          <video
            @mouseover="strictPlay"
            v-else-if="p['media_type'] == 2"
            :src="p['video_versions'][0].url"
          ></video>
          <img
            v-else
            :src="p['image_versions']['815']"
            alt=""
            srcset=""
          >
          <img
            alt=""
            srcset=""
          >
          <p> {{p.caption.text}} </p>
        </div>

      </div>
      <button
        @click="closeLightbox"
        class="close-button"
      >&times;</button>
    </div>
  </section>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
      lightboxVisibility: false,
      imageSrc: "",
      swiperOption: {
        speed: 300,
        centeredSlides: false,
        pagination: {
          el: ".swiper-pagination",
          type: "bullets"
        },
        clickable: true,
        on: {
          slideChangeTransitionEnd: async function() {
            let vid = this.$el[0].parentElement.querySelector(
              ".swiper-slide-active video"
            );
            let all = this.$el[0].parentElement.querySelectorAll(
              ".swiper-slide video"
            );
            all.forEach(e => e.pause());
            try {
              await vid.play();
            } catch {}
          }
        }
      }
    };
  },
  props: {
    src: String,
    fullData: Object,
    visible: Boolean,
    index: String,
    data: Object
  },
  computed: {
    lightboxIndex: {
      get() {
        return this.index;
      },
      set() {
        this.imageIndex;
      }
    },
    swiper() {
      return this.$refs.insSwiper.$swiper;
    },
    lightboxSource: {
      get() {
        return this.src;
      },
      set() {
        this.imageSrc;
      }
    },
    lightboxData() {
      return this.fullData.feeds;
    }
  },
  methods: {
    closeLightbox() {
      this.lightboxVisibility = false;
      this.$emit("visibilityChanged", this.lightboxVisibility);
      this.imageSrc = "";
      this.instagramScroll();
    },
    instagramScroll() {
      document.getElementsByTagName("video").forEach(vid => vid.pause());
    },
    playVid: async function(e) {
      let v = e.target;
      if (v.paused) {
        try {
          await v.play();
        } catch {}
      } else {
        v.pause();
      }
    },
    strictPlay(e) {
      e.target.play();
    }
  },
  created() {
    document.onkeydown = evt => {
      evt = evt || window.event;
      if (evt.keyCode == 27) {
        this.closeLightbox();
      }
    };
  },
  watch: {
    visible: function(prev, next) {
      if (prev) {
        setTimeout(() => {
          document.getElementById(this.index).scrollIntoView("smooth");
        }, 1000);
      }
      this.lightboxVisibility = prev;
    }
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>

