<template>
  <section class="instagram">
    <div
      v-if="instaData && followerCount && feeds"
      class="instagram-wrapper"
    >
      <innerPageHeader
        title="Instagram"
        :strong="followerCount.toString()"
        desc="Takipci"
      />
      <div
        ref="instagram"
        class="instagram-post-wrapper"
      >
        <div class="scroller">
          <div
            v-for="instagramPost in feeds.slice(0,34)"
            :key="instagramPost.id"
            class="instagram-card"
            @click="openLightbox(instagramPost.id, instagramPost['image_versions']['480'])"
            :class="Math.ceil(Math.random() * 2.5) > 1 ? 'two' : 'one'"
          >
            <img
              :src="instagramPost['image_versions']['191']"
              alt=""
              srcset=""
            >
            <p> {{instagramPost.caption.text | trim}}... </p>
          </div>
        </div>
        <div class="scroller">
          <div
            v-for="instagramPost in feeds.slice(35,70)"
            :key="instagramPost.id"
            class="instagram-card"
            @click="openLightbox(instagramPost.id, instagramPost['image_versions']['480'])"
            :class="Math.ceil(Math.random() * 2.5) > 1 ? 'two' : 'one'"
          >
            <img
              :src="instagramPost['image_versions']['191']"
              alt=""
              srcset=""
            >
            <p> {{instagramPost.caption.text | trim}}... </p>
          </div>
        </div>
      </div>
      <InstagramLightbox
        :index="imageIndex"
        :fullData="igData"
        :src="chosenImage"
        @visibilityChanged="updateHere"
        :visible="visible"
      />
    </div>
  </section>
</template>

<script>
import InstagramLightbox from "@/components/HomeDir/InstagramLightbox.vue";
import innerPageHeader from "@/components/Common/InnerPageHeader.vue";
export default {
  data: () => ({
    visible: false,
    instaData: {},
    igData: {},
    chosenImage: "",
    imageIndex: ""
  }),
  computed: {
    followerCount() {
      return this.instaData["follower_count"];
    },
    feeds() {
      return this.instaData["feeds"];
    }
  },
  methods: {
    httpGet: function(theUrl) {
      var xmlHttp = new XMLHttpRequest();
      xmlHttp.open("GET", theUrl, false);
      xmlHttp.send(null);
      this.instaData = JSON.parse(xmlHttp.responseText);
    },
    openLightbox(a, b) {
      this.chosenImage = b;
      this.igData = this.instaData;
      document.body.classList.add("no-scroll");
      this.visible = true;
      this.imageIndex = a;
    },
    updateHere() {
      document.body.classList.remove("no-scroll");
      this.visible = false;
    }
  },
  filters: {
    trim: a => a.substr(0, 130)
  },
  mounted() {
    this.httpGet("https://drserkanaygin.com/api/instagram/drserkanaygin");
  },
  components: {
    innerPageHeader,
    InstagramLightbox
  }
};
</script>

