.instagram {
  padding-top: 10px;
  padding-bottom: 40px;
}
.instagram-post-wrapper {
  display: block;
  overflow: hidden;
  flex-wrap: wrap;
  .scroller {
    overflow-x: scroll;
    display: flex;
  }
  .instagram-card {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 191px;
    margin: 5px;
    border-radius: 10px;
    height: 191px;
    object-fit: cover;
    overflow: hidden;
    &.two {
      min-width: 300px;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
    }
    p {
      z-index: 1;
      color: white;
      font-size: 14px;
      padding: 0 20px;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      display: block;
      width: 100%;
      max-height: 191px;
      max-width: 100%;
      height: auto;
      filter: blur(2px);
    }
  }
}
@keyframes autoscroll {
  to {
    transform: translateX(-100%);
  }
}
</style>