<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
      swiperOption: {
        slidesPerView: 4,
        breakpoints: {
          300: {
            slidesPerView: 1,
            spaceBetween: 0
          },
          768: {
            slidesPerView: 1,
            spaceBetween: 0
          },
          1100: {
            slidesPerView: 3
          },
          1360: {
            slidesPerView: 4
          },
          1500: {
            slidesPerView: 4
          }
        },
        spaceBetween: 30,
        centeredSlides: false,
        parallax: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      }
    };
  },
  props: {
    hotNews: {
      type: Array
    }
  },
  computed: {
    eligable() {
      if (this.hotNews) {
        return this.hotNews.filter(e => e.body != undefined);
      }
    }
  },
  components: {
    swiper,
    swiperSlide
  }
};
</script>

