<template>
  <section class="home">
    <MainSwiper :swiperData="sliders" />
    <ServicesTabs />
    <DoctorsSwiper :swiperData="doctors" />
    <WhoAreWe />
    <HotNews :hot-news="entries" />
    <ClientSwiper />
    <Instagram />
    <Form />
  </section>
</template>

<script>
// @ is an alias to /src
import { gql } from "apollo-boost";
import Instagram from "@/components/HomeDir/Instagram.vue";
import MainSwiper from "@/components/Common/MainSwiper.vue";
import ServicesTabs from "@/components/HomeDir/ServicesTab.vue";
import DoctorsSwiper from "@/components/HomeDir/DoctorsSwiper.vue";
import WhoAreWe from "@/components/HomeDir/WhoAreWe.vue";
import HotNews from "@/components/HomeDir/HotNews.vue";
import ClientSwiper from "@/components/HomeDir/ClientSwiper.vue";
import Form from "@/components/Common/Form.vue";
import Footer from "@/components/Common/Footer.vue";

export default {
  name: "Home",
  components: {
    MainSwiper,
    ServicesTabs,
    DoctorsSwiper,
    WhoAreWe,
    HotNews,
    ClientSwiper,
    Form,
    Instagram,
    Footer
  },
  apollo: {
    sliders: gql`
      query {
        sliders: entries(section: "slider", siteId: "1") {
          title
          url
          slug
          ... on slider_slider_Entry {
            subtitle
            image {
              title
              url
            }
            buttons {
              ... on buttons_internalLink_BlockType {
                typeHandle
                title
                color
                page {
                  title
                  url
                }
              }
              ... on buttons_externalLink_BlockType {
                typeHandle
                title
                color
                button_url
              }
            }
          }
        }
      }
    `,
    doctors: gql`
      query {
        doctors: entries(section: "doctors") {
          title
          ... on doctors_doctors_Entry {
            subtitle
            image {
              url
            }
            buttons {
              ... on buttons_internalLink_BlockType {
                typeHandle
                title
                color
                page {
                  title
                  url
                }
              }
              ... on buttons_externalLink_BlockType {
                typeHandle
                title
                color
                button_url
              }
            }
          }
        }
      }
    `,
    entries: gql`
      query {
        entries(limit: 15, orderBy: "dateCreated DESC") {
          title
          ... on pages_pages_Entry {
            title
            body
          }
        }
      }
    `
  }
};
</script>

<style>
.home {
  z-index: 89;
}
</style>
