<template>
  <div>
    <transition
      :mode="mode"
      :name="transition"
    >
      <slot></slot>
    </transition>
    <div class="overlay-top"></div>
    <div
      class="overlay-right"
      :style="bg"
    ></div>
    <div class="overlay-bottom"></div>
    <div
      :style="bg"
      class="overlay-left"
    ></div>
  </div>
</template>

<script>
export default {
  props: {
    name: {
      type: String
    },
    bg: {
      type: String,
      default:
        "background-image: url(https://cdn.dokumedical.com/wp-content/uploads/2019/11/doku_site_banner_1-min.jpg)"
    }
  },
  data() {
    return {
      transition: "overlay-left-right",
      mode: "in-out"
    };
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      this.transition = to.meta.transition
        ? to.meta.transition
        : this.$props.name;
      next();
    });
  }
};
</script>

<style lang="scss">
.overlay-left-right-enter ~ .overlay-left,
.overlay-left-right-enter ~ .overlay-right {
  width: 0;
}

.overlay-left-right-leave-active ~ .overlay-left,
.overlay-left-right-leave-to ~ .overlay-left,
.overlay-left-right-leave-to ~ .overlay-right,
.overlay-left-right-leave-to ~ .overlay-right {
  width: 50vw;
}

.overlay-left-right-enter-active ~ .overlay-left,
.overlay-left-right-enter-active ~ .overlay-right {
  transition-timing-function: ease;
}

.overlay-left-right-leave-active ~ .overlay-left,
.overlay-left-right-leave-active ~ .overlay-right {
  transition-timing-function: ease;
}

.overlay-left-right-enter-active,
.overlay-left-right-leave-active {
  transition-duration: 600ms;
}
.overlay-left,
.overlay-right {
  z-index: 100;
}
.overlay-left {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 0;
  background-size: 200% 100%;
  background-position: 100% 100%;

  transition-duration: 200ms;
}
.overlay-right {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 0;
  background-size: 200% 100%;
  background-position: 0% 100%;

  transition-duration: 200ms;
}
</style>