    toHT(a, b) {
      switch (b) {
        case "Sa Ekimi":
          document.location.href.includes("sac-ekimi")
            ? this.$router.push(a.replace("", "s"))
            : this.$router.push("/sac-ekimi/" + a.replace("", "s"));
          break;
        case "Estetik Cerrahi":
          document.location.href.includes("medikal-estetik")
            ? this.$router.push(a.replace("", "s"))
            : this.$router.push("/medikal-estetik/" + a.replace("", "s"));
